import { z } from 'zod';
import { WhenItemsInterface, WhenItemsSchema } from './when-items.interface';

export const SwitchSchema = z.object({
  when: WhenItemsSchema,
  else: z.string().min(1).optional(),
  segment: z.boolean().optional(),
});

export interface SwitchInterface {
  when: WhenItemsInterface;
  else?: string;
  segment?: boolean;
}
